import {Pipe,PipeTransform} from '@angular/core';
@Pipe({
  name:'hunkCurrency'

})
export class hunkCurrencyPipe implements PipeTransform {
  transform(value:string, args:any[]){
    let newValue:string =value.replace(",", " ");
    newValue=newValue.replace(".", ",");
    return newValue+' Ft.';
  }
}